.card {
	background: #fff;
	border-radius: 2px;
	width: 20%;
	height: 200px;
	margin: 1rem;
	position: relative;
	-webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	        box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	text-align: left;
	float: left;
}

.card > .img-container {
	height: 100%;
	overflow: hidden;
	text-align: center;
	background:#b3e0dd;
}
.card > .img-container > img {
	width: 80%
}

.card > .content {
	padding-left: 1rem;
	padding-right: 1rem;
	font-size: 15px;
}
.card:hover {
    -webkit-transform:scale(1.15); /* Safari and Chrome */ /* Firefox */
    -ms-transform:scale(1.15); /* IE 9 */ /* Opera */
     transform:scale(1.15);
}





